@import "./common/root" , 
        "./layout/grid" , 
        "./common/common";


.wrapper {
    // position: fixed;
    // top: 0;
    // left: 0;
    // width: 100%;
    // height: 100%;
    // overflow: hidden;
}

.wrapper::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(0,0,0);
    opacity: 0.9;
    z-index: 998;
    display: none;
}

.wrapper.active::before {
    display: block;
}

.slider {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: 1s;
    &.active {
        opacity: 1;
        visibility: visible;
    }

    &-slide {
        width: 100%;
        height: 100%;
    }
}

.black{
    background: #000;
}
.orange {
    background: orange;
}

.intro {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 1s;
    padding: 0 10px;

    &.slide {
        opacity: 0;
        visibility: hidden;
        top: -100%;
    }

    
    &__uptitle {
        font-family: "Maximian";
        text-transform: uppercase;
        font-size: clamp(42px,calc(71vw/14.4),80px);
        line-height: clamp(62px,calc(100vw/14.4),110px);
        background: linear-gradient(180deg, rgba(12, 11, 11, 0.2) 0%, rgba(12, 11, 11, 0) 100%),linear-gradient(180deg, #00FDEE 0%, rgba(0, 253, 238, 0) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

    }

    &__img {
        width: 100%;
        height: clamp(100px,calc(200vw/14.4),250px);
        max-height: 100%;
        object-fit: cover;
    }
    &__button {
        text-transform: uppercase;
        font-family: "Rapidtech";
        font-size: clamp(42px,calc(52vw/14.4),60px);
        line-height: clamp(42px,calc(52vw/14.4),60px);
        text-align: center;
        color: #fff;
        background: linear-gradient(180deg, #13124A -31.23%, #1FF7F7 50.68%, #1B1A58 122.85%);
        border-radius: 25px;
        border: none;
        padding: 15px 20px;
        cursor: pointer;

    }
}

.pro {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    &__content {
        width: 50%;
        height: 100%;
        background: rgb(47,137,227);
        background: linear-gradient(100deg, rgba(47,137,227,1) 0%,rgba(47,137,227,0.3) 70%,rgba(18,47,65,0.7) 90% ,rgba(18,47,65,1) 100%);
        -webkit-box-shadow: -29px -17px 51px 0px rgba(18,47,65,1) inset;
        -moz-box-shadow: -29px -17px 51px 0px rgba(18,47,65,1) inset;
        box-shadow: -29px -17px 51px 0px rgba(18,47,65,1) inset;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        @include media(1200px){
            width: 35%;
        }
        @include media($md) {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 20%;
        }

        // overflow: hidden;

        &-border {
            width: clamp(30px,calc(50vw/14.4),60px);
            height: 150%;
            background: #fff;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            position: absolute;
            top: -10%;
            right: 0;
            transform: rotate(15deg);

            @include media(1200px){
                transform: rotate(6deg);
            }
            @include media($md){
                display: none;
            }

            &-part {
                flex: auto;
                border-top: 1px solid #000;
                border-bottom: 1px solid #000;
                border-left: 5px solid #000;
                border-right: 5px solid #000;
            }
        }
    }



    &__desc {
        width: 40%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // justify-content: center;

        @include media(1200px){
            width: 55%;
        }
        @include media($md){
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 80%;
            text-align: center;
            padding-top: 1%;
        }

        &-title {
            font-family: "RedRose";
            font-weight: 700;
            font-size: clamp(32px,calc(72vw/14.4),80px);
            line-height: clamp(40px,calc(82vw/14.4),90px);
            border-bottom: 2px solid #fff;
            color: #fff;
        }

        &-list {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 100%;
            justify-content: space-evenly;

            & li {
                display: flex;
                align-items: center;
                gap: 20px;

                & span {
                    font-family: "serif";
                    font-size: clamp(14px,calc(40vw/14.4),45px);
                    line-height: clamp(20px,calc(50vw/14.4),55px);
                    color: #fff;
                }
            }

            @include media($md){
                padding: 0 20%;
            }
        }

        &-img {
            width: clamp(30px,calc(50vw/14.4),60px);
            height: clamp(30px,calc(50vw/14.4),60px);
            object-fit: contain;
        }

        &-subtitle {
            font-family: "Kurale";
            font-size: clamp(18px,calc(40vw/14.4),50px);
            line-height: clamp(24px,calc(60vw/14.4),70px);
            color: #fff;
        }
    }
    
    &__img {
        // position: absolute;
        // top: 3%;
        // left: 3%;
        // max-width: clamp(200px,calc(900vw/14.4),1200px);
        // width: 100%;
        // height: clamp(300px,calc(700vw/14.4),800px);

        width: 400px;
        height: 500px;
        position: absolute;
        top: -15%;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;

        & img {
            // width: 100%;
            // height: 100%;
            // object-fit: contain;

            padding-top: 10%;
            width: 100%;
            height: 100%;
            display: block;
            margin: 0 auto;
            border: none;
            object-fit: cover;
        }

        @include media($md) {
            height: 110%;
            width: clamp(100px,calc(150vw/14.4),170px);
            inset: 0;
        }
    }

    .circle {
        background: blue;
        position: absolute;
        border-radius: 50%;
        animation-name: circleAnimation;
        animation-duration: 10s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in;
    }
    @keyframes circleAnimation{
        from {
            transform: translateY(1000px);
            opacity: 1;
        }
        to {
            transform: translateY(-1000px);
            opacity: 0.3;
        }
    }
}

.nav {

    &__list {
        position: fixed;
        z-index: 999;
        left: 0;
        height: clamp(30px,calc(60vw/14.4),100px);
        width: 100%;
        top: -100%;
        transition: 0.5s;
        display: flex;
        align-items: center;
        justify-content: space-between;
        overflow: scroll;

        @include media($xs) {
            width: 100%;
        }

        @include media($sm) {
            flex-direction: column;
            justify-content: space-between;
            width: 50%;
            height: 100%;
            background: #fff;
            align-items: start;
            padding-left: 10%;
            top: 0;
            left: -100%;

            &.active {
                left: 0;
            }
        }
        
        &.active {
            top: 0;
        }
    }

    &__item {
        flex: auto;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #000;
        background: #fff;

        @include media($sm) {
            border: none;
            transform: translateX(-60px);
            opacity: 0;
            transition: 1s;

            &.active {
                transform: translateX(0);
                opacity: 1;
            }
        }



    }

    &__link {
        font-family: "Gochi";
        font-size: clamp(24px,calc(40vw/14.4),50px);
        line-height: clamp(30px,calc(46px,56px));
        color: #000;
    }

    &__logo {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &__lang {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    

    &__title {
        position: fixed;
        top: 0;
        left: 5%;
        padding: 5px clamp(10px,calc(20vw/14.4),24px);
        font-size: clamp(18px,calc(32vw/14.4),36px);
        line-height: clamp(20px,calc(36vw/14.4),40px);
        background: #fff;
        font-family: "Gochi";
        border-bottom-right-radius: clamp(10px,calc(20vw/14.4),30px);
        border-bottom-left-radius: clamp(10px,calc(20vw/14.4),30px);
        cursor: pointer;
        opacity: 0;
        visibility: hidden;
        transition: 0.6s;
        &.active {
            opacity: 1;
            visibility: visible;
        }
    }

    &__cancel {
        font-size: 34px;
        position: absolute;
        top: 10px;
        right: 10px;
        display: none;
    
        @include media($xs) {
            display: inline-block;
        }
    }
}