* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

body {
    background: rgb(18,47,65);
}

body.locked {
    overflow: hidden
}

.container {
    @include container();
}

.row {
    @include row();
}

::before,
::after {
    font-family: "fontawesome";
}

.burger::before {
    content: "\f805";
    display: inline-block;
    font-size: 32px;
    position: fixed;
    top: 30px;
    right: 30px;
    z-index: 10;
    color: #fff;
    display: none;

    @include media($md) {
        display: inline-block;
    }
}
.cancel::before {
    content: '\f410';
}